<template>
	<div class="meal-content">
			<div class="meal-content-title"><i class="bar bar-red"></i> {{sourceData.title}}</div>
			<div class="meal-content-body">
				<div
					:class="['dsc-' + detail.type]"
					v-for="detail in sourceData.content"
				>
					<img :src="detail.content" v-if="detail.type == 'img'">
					<div v-if="detail.type == 'txt'">
						<span :class="[content.size && 'font-' + content.size, { 'font-medium': content.font == 'medium' }]" v-for="content in detail.content">
							<a :ref="content.anchor" v-bind="{ name: content.anchor }" v-if="content.anchor"></a>

							<a :class="[content.align && 'font-' + content.align]" :href="content.url" v-if="content.target != 'router-link' && typeof(content.content) != 'object'">{{content.content}}</a>

							<a :class="[content.align && 'font-' + content.align]" :href="content.url" v-if="typeof(content.content) == 'object'" v-for="c in content.content">{{c}}</a>
							
							<router-link tag="a" :to="{ path: content.url }" v-if="content.target == 'router-link'">{{content.content}}</router-link>
						</span>
					</div>
					<div class="table" v-if="detail.type == 'table'">
						<div class="tr" v-for="tr in detail.content">
							<div class="td" v-for="td in tr">
								<div v-if="!Number(content.content)" :class="[content.col && 'col-' + td.col]" v-for="content in td.content">{{content.content}}</div>

								<div v-if="Number(content.content)" :class="[content.col && 'col-' + td.col]" v-for="content in td.content"><div class="price">{{content.content | thousandth}}</div><div class="unit">JPY</div></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
</template>

<script>
	export default {
		props: ['data'],
		data() {
			return {
				sourceData: {}
			}
		},
		watch: {
			data(newValue) {
				this.sourceData = newValue;
			}
		}
	}
</script>